<template>
	<view>
		<category :categoryList="categoryList" :subCategoryList="subCategoryList" @categoryMainClick="categoryMainClick"
		 @categorySubClick="categorySubClick"></category>
	</view>
</template>

<script>
	import category from "../../components/qiyue-category/qiyue-category.vue"
	export default {
		components: {
			category
		},
		data() {
			return {
				categoryList: [],
				subCategoryList: []
			}
		},
		methods: {
			categoryMainClick(category) {
				this.subCategoryList = category.subCategoryList;
			},
			categorySubClick(category) {
				this.getVideoDetail(category)
			},
			getVideoDetail(category) {
				let rex = /\/(\d+)\//g
				let id = rex.exec(category.link)[1]
				this.$request.httpRequest({
					url: 'videoDetail?id=' + id,
					method: 'delete'
				}).then((res) => {
					console.log("res:", res.item_list[0].video.play_addr.url_list[0])
					uni.navigateTo({
						url:'../viewVideo/viewVideo'
					})
				})
			},
			getHotDouyin() {
				this.$request.httpRequest({
					url: 'hotspotRise'
				}).then((res) => {
					let tempArr = []
					res.billboard_data.map(v => {
						if (v.extra_list) {
							let {
								img_url,
								link,
								sec_item_id,
								title
							} = v.extra_list[0]
							let tempObj = {}
							tempObj.name = title
							tempObj.logo = img_url
							tempObj.link = link
							tempObj.value = v.value
							tempArr.push(tempObj)
						}
					})
					this.categoryList.push({
						"name": '热搜榜',
						"subCategoryList": tempArr
					})
					this.subCategoryList = this.categoryList[0].subCategoryList;
				})
			},
			getHotSearch() {
				this.$request.httpRequest({
					url: 'hotSearch'
				}).then((res) => {
					let tempArr = []
					res.billboard_data.map(v => {
						if (v.extra_list) {
							let {
								img_url,
								link,
								sec_item_id,
								title
							} = v.extra_list[0]
							let tempObj = {}
							tempObj.name = title
							tempObj.logo = img_url
							tempObj.link = link
							tempObj.value = v.value
							tempArr.push(tempObj)
						}
					})
					this.categoryList.push({
						"name": '热点上升榜',
						"subCategoryList": tempArr
					})
				})
			},
			getPopular() {
				this.$request.httpRequest({
					url: 'popular'
				}).then((res) => {
					let tempArr = []
					res.billboard_data.map(v => {
						let {
							img_url,
							link,
							sec_item_id,
							title,
							value
						} = v
						let tempObj = {}
						tempObj.name = title
						tempObj.logo = img_url
						tempObj.link = link
						tempObj.value = value
						tempArr.push(tempObj)
					})
					this.categoryList.push({
						"name": '今日热门榜',
						"subCategoryList": tempArr
					})
				})
			}
		},
		created() {
			this.getHotDouyin()
			this.getHotSearch()
			this.getPopular()
		}
	}
</script>

